<template>
    <div class="resume-container">
      <h1>简历</h1>
      <div v-if="safeMarkdownContent" v-html="safeMarkdownContent" class="markdown-body"></div>
      <div v-else>
        <p>Failed to load personal introduction.</p>
      </div>
      <router-link to="/">返回首页</router-link>
    </div>
  </template>
  
  <script>
  import { ref, onMounted } from 'vue';
  import MarkdownIt from 'markdown-it';
  import DOMPurify from 'dompurify';
  
  export default {
    name: 'ResumePage',
    setup() {
      const markdownContent = ref('');
      const safeMarkdownContent = ref('');
      const md = new MarkdownIt();
  
      onMounted(async () => {
        try {
          const response = await fetch('/introduction.md');
          if (!response.ok) {
            throw new Error(`HTTP error! status: ${response.status}`);
          }
          const mdText = await response.text();
          markdownContent.value = md.render(mdText);
          safeMarkdownContent.value = DOMPurify.sanitize(markdownContent.value);
        } catch (error) {
          console.error('Failed to load markdown file:', error);
          markdownContent.value = 'Failed to load personal introduction.';
          safeMarkdownContent.value = 'Failed to load personal introduction.';
        }
      });
  
      return {
        safeMarkdownContent,
      };
    },
  };
  </script>
  
  <style scoped>
  /* 引入 variables */
  :root {
    --primary-color: #e91e63;
    --text-color: #f0f0f0;
    --background-color: #121212;
    --secondary-background-color: #212121;
    --gray-color: #9e9e9e;
    --accent-color: #80deea;
    --hover-accent-color: #4db6ac;
  }
  
  /* 通用样式 */
  .resume-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: var(--secondary-background-color); /* Use secondary background */
    border: 1px solid var(--primary-color);
    border-radius: 5px;
    font-family: 'Roboto', sans-serif; /* Use Roboto font */
    color: var(--text-color);
  }
  
  h1 {
    text-align: left;
    margin-bottom: 20px;
    color: var(--primary-color);
    font-size: 2.4rem;
    letter-spacing: 0.15rem;
    border-bottom: 3px solid var(--primary-color);
    padding-bottom: 10px;
    text-transform: uppercase;
    transform: skewY(-3deg);
  }
  
  /* Markdown body 样式 */
  .markdown-body {
    line-height: 1.6;
    color: var(--text-color);
  }
  
  .markdown-body h1,
  .markdown-body h2,
  .markdown-body h3,
  .markdown-body h4,
  .markdown-body h5,
  .markdown-body h6 {
    color: var(--text-color);
    margin-top: 1em;
    margin-bottom: 0.5em;
    line-height: 1.2;
  }
  
  .markdown-body h1 {
    font-size: 2.2em;
    border-bottom: 1px solid var(--primary-color); /*  Use primary color */
    padding-bottom: 0.3em;
  }
  
  .markdown-body h2 {
    font-size: 1.8em;
  }
  
  .markdown-body h3 {
    font-size: 1.4em;
  }
  
  .markdown-body p {
    margin-bottom: 1em;
    color: #ccc;
    font-size: 1.1rem;
  }
  
  .markdown-body ul,
  .markdown-body ol {
    padding-left: 2em;
    margin-bottom: 1em;
  }
  
  .markdown-body li {
    margin-bottom: 0.5em;
  }
  
  .markdown-body a {
    color: var(--accent-color); /*  Use accent color */
    text-decoration: none;
    transition: color 0.3s ease;
  }
  
  .markdown-body a:hover {
    text-decoration: underline;
    color: var(--hover-accent-color); /*  Use hover accent color */
  }
  
  .markdown-body strong,
  .markdown-body b {
    font-weight: bold;
  }
  
  .markdown-body em,
  .markdown-body i {
    font-style: italic;
  }
  
  .markdown-body code {
    background-color: #333;
    color: var(--text-color);
    padding: 0.2em 0.4em;
    border-radius: 3px;
    font-family: 'Courier New', monospace;
  }
  
  /* 返回首页链接样式 */
  a {
    display: inline-block;
    margin-top: 20px;
    color: #000; /* text color black*/
    text-decoration: none;
    border: 1px solid var(--primary-color);
    background-color: var(--primary-color);
    padding: 8px 15px;
    border-radius: 5px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  a:hover {
    background-color: #000; /* background black */
    color: var(--primary-color);
  }
  </style>