import { createRouter, createWebHistory } from 'vue-router';
import MyPage from '../components/MyPage.vue'; // 导入你的 MyPage 组件
import ResumePage from '../components/ResumePage.vue'; // 导入 ResumePage 组件

const routes = [
  {
    path: '/',
    name: 'MyPage',
    component: MyPage,
  },
  {
    path: '/resume',
    name: 'ResumePage',
    component: ResumePage,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});

export default router;